<template>
    <Header :title="address" />
    <van-address-edit :area-list="areaList" :show-delete="address == '地址编辑'" :address-info="addressInfo" show-set-default
        :area-columns-placeholder="['请选择', '请选择', '请选择']" @save="onSave" @delete="onDelete" />
</template>
<script>
import store from "@/store";
import Header from "@/components/Header";
import { reactive, toRefs, computed, onMounted } from "vue"
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex"
import { showToast } from 'vant';

export default {
    components: {
        Header
    },
    setup() {

        let router = useRouter()
        let route = useRoute()
        let store = useStore()

        let data = reactive({
            areaList: {
                // 110000：省，加100：市，加1：区
                province_list: {
                    110000: "黑龙江省",
                    120000: "辽宁省",
                },
                city_list: {
                    110100: "哈尔滨市",
                    110200: "大庆市",
                    120100: "沈阳市",
                    120200: "大连市",
                },
                county_list: {
                    110101: "南岗区",
                    110102: "香坊区",
                    120102: "铁西区",
                    120202: "旅顺口区",
                },
            },
            addressInfo: {}
        })

        let onSave = (info) => {
            if (route.query.type == "add") {
                if(store.state.userAddress.length){
                    info.id=Number(store.state.userAddress[store.state.userAddress.length-1].id)+1
                }else{
                    info.id=1001
                }
                store.commit("ADDADDRESS",info)
            }else{
                store.commit('CHANGEADDRESS', info)
            }
            showToast("保存成功")
            setTimeout(() => {
                router.back()
            }, 1000);
        }

        let onDelete = (info) => {
            store.commit("DELETEADDRESS", info)
            showToast("删除成功")
            setTimeout(() => {
                router.back()
            }, 1000);
        }

        let address = computed(() => {
            return route.query.type == "add" ? "地址新增" : "地址编辑"
        })

        let init = () => {
            store.state.userAddress.forEach(item => {
                if (item.id == Number(route.query.id)) {
                    data.addressInfo = item
                }
            })
        }

        onMounted(() => {
            init()
        })

        return {
            ...toRefs(data),
            onSave,
            onDelete,
            address
        }
    }
}
</script>
<style scoped lang="less"></style>